import Vue from 'vue'
import Vuex from 'vuex'



Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 缓存数据

    // banner数据
    bannerDataCache: [],

    // 精品推荐食品数据
    recommendDataCache: [],

    // 商品详情数据
    productDataCache: [],

    // 商品种类
    proTypeDataCache: [],


  },
  mutations: {

    // 缓存数据方法
    changeData(state, data) {
      state[data.key] = data.value;
    },

  }
}



)
